<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-upload
      style="
        margin-left: 35%;
        margin-top: 200px;"
      drag
      ref="upload"
      :action="uploadURL"
      multiple
      :on-success="handleSuccess"
      :on-error="handleError"
      accept="dat">
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><em></em></div>
      <div class="el-upload__tip" slot="tip">.dat</div>
      </el-upload>
      <el-alert
      style="width: 50%;
        margin-left :25%;
        margin-top :20px;"
      title=""
      type="warning"
      description="(),"
      show-icon>
    </el-alert>
  </div>
</template>

<script>
import axios from '../../services/my-axios'
export default {
  data () {
    return {
      fileList: []
    }
  },
  computed: {
    uploadURL () {
      return axios.baseURL + '/qiandaoauto'
    }
  },
  methods: {
    handleSuccess () {
      this.$message({
        message: '',
        type: 'success'
      })
    },
    handleError () {
      this.$message({
        message: ',',
        type: 'warning'
      })
    },
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
    },
    handleExceed (files, fileList) {
    },
    beforeRemove (file, fileList) {
    }
  }
}
</script>


